import React from 'react';

import { TourStep } from '../../../api/src/domain/TourStep';
import { Mask } from './Mask';
import { TourStepPopup } from './TourStepPopup';

export type TourSequenceProps = {
  onComplete: (index: number) => void;
  tour: TourStep[];
};

export const TourSequence: React.FC<TourSequenceProps> = ({
  onComplete,
  tour,
}) => {
  const [stepIndex, setStepIndex] = React.useState<number>(-1);
  const [nextStepIndex, setNextStepIndex] = React.useState<number>();

  /** This is just here so we can trigger a render on scroll... there must be a better way. */
  const [, setScrollYPosition] = React.useState<number>(window.scrollY);

  const stepInfos = React.useMemo(() => {
    return tour.map((step) => ({
      step,
      targetEl:
        ((): HTMLElement | null => {
          switch (step.selector?.type) {
            case 'css':
              return document.querySelector<HTMLElement>(step.selector.value);
            case 'xpath':
              return document.evaluate(
                step.selector.value,
                document,
                null,
                XPathResult.FIRST_ORDERED_NODE_TYPE,
                null,
              ).singleNodeValue as HTMLElement | null;
            case undefined:
              return null;
          }
        })() ?? undefined,
    }));
  }, [tour]);

  React.useEffect(() => {
    setTimeout(() => setStepIndex(0), 1000);

    const handler = () => setScrollYPosition(window.scrollY);
    window.addEventListener('scroll', handler);
    return () => window.removeEventListener('scroll', handler);
  }, []);

  return (
    <>
      {stepInfos.map(({ step, targetEl }, i) => {
        return (
          <div key={i}>
            {step.mask ? (
              <Mask
                active={
                  nextStepIndex !== -1 &&
                  ((stepIndex === i && nextStepIndex === undefined) ||
                    nextStepIndex === i)
                }
                key={`mask-${i}`}
                style={(() => {
                  const rect = targetEl?.getBoundingClientRect();
                  const x = rect?.x ?? 0;
                  const y = rect?.y ?? 0;
                  const width = rect?.width ?? 0;
                  const height = rect?.height ?? 0;
                  const borderBottomWidth = `calc(100vh - ${y + height}px)`;
                  return {
                    borderBottomWidth,
                    borderLeftWidth: targetEl?.getBoundingClientRect().x,
                    borderRightWidth: `calc(100vw - ${x + width}px)`,
                    borderTopWidth: targetEl?.getBoundingClientRect().y,
                  };
                })()}
              />
            ) : undefined}
            <TourStepPopup
              active={nextStepIndex === undefined && stepIndex === i}
              buttonDismissText={
                step.buttons?.find((b) => b.action === 'dismiss')?.text
              }
              buttonNextText={
                step.buttons?.find((b) => b.action === 'next')?.text
              }
              html={step.html}
              key={`popup-${i}`}
              onDismiss={() => setNextStepIndex(-1)}
              onOut={() => {
                if (nextStepIndex === -1) {
                  onComplete(i);
                } else if (nextStepIndex !== undefined) {
                  setStepIndex(nextStepIndex);
                  setNextStepIndex(undefined);
                }
              }}
              onNext={
                i < tour.length - 1 ? () => setNextStepIndex(i + 1) : undefined
              }
              targetEl={targetEl}
            />
          </div>
        );
      })}
    </>
  );
};
