import { styled } from '@material-ui/styles';
import React from 'react';
import { ReactNode } from 'react';
import { CSSTransition } from 'react-transition-group';

import { FlexColumn, FlexContent, FlexRow } from './Flex';
import { Popper } from './Popper';

export type PopupProps = {
  anchorEl?: HTMLElement;
  buttons?: ReactNode[];
  onOutEnd?: () => void;
  open: boolean;
};

const Content = styled(FlexContent)({
  fontSize: '1.2em',
  padding: '1em',
});

const Buttons = styled(FlexRow)({
  '& > *': {
    marginLeft: '1em',
  },
  justifyContent: 'flex-end',
  padding: '1em',
});

const Systray = styled('div')({
  bottom: '2em',
  height: 'auto',
  maxWidth: '35em',
  position: 'fixed',
  right: '2em',
});

const Inner = styled(FlexColumn)({
  backgroundColor: 'white',
  borderRadius: '1em',
  position: 'relative',
});

const TransitionSlideFade = styled('div')({
  '&, &.enter, &.exit.exit-active, &.exit-done': {
    opacity: 0,
    top: '2em',
  },
  '&.enter-active, &.exit-active': {
    transition: 'opacity 250ms ease-in, top 250ms ease-in',
  },
  '&.enter.enter-active, &.enter-done, &.exit': {
    opacity: 1,
    top: 0,
  },
  '[data-popper-placement="bottom"] &::before': {
    left: 'calc(50% - 0.5em)',
    top: '-0.5em',
  },
  '[data-popper-placement="left"] &::before': {
    right: '-0.5em',
    top: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement="right"] &::before': {
    left: '-0.5em',
    top: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement="top"] &::before': {
    bottom: '-0.5em',
    left: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement] &::before': {
    backgroundColor: 'white',
    boxShadow:
      '0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12)',
    content: '""',
    display: 'block',
    height: '1em',
    position: 'absolute',
    transform: 'rotate(45deg)',
    width: '1em',
  },
  borderRadius: '1em',
  boxShadow:
    '0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12)',
  position: 'relative',
});
export const Popup: React.FC<PopupProps> = ({
  anchorEl,
  buttons = [],
  children,
  onOutEnd = () => undefined,
  open,
}) => {
  const transitionRef = React.useRef(null);
  const [popperActive, setPopperActive] = React.useState(open);
  const [popperClosed, setPopperClosed] = React.useState(false);

  React.useEffect(() => {
    if (open && !popperActive && !popperClosed) {
      setPopperActive(true);
    } else if (popperClosed) {
      setPopperActive(false);
      setPopperClosed(false);
    }
  }, [open, popperActive, popperClosed]);

  return anchorEl !== undefined ? (
    <Popper
      anchorEl={anchorEl}
      offset={12}
      open={popperActive}
      placement="right"
      style={{ maxWidth: '35em', zIndex: 1112 }}
    >
      <CSSTransition
        onExited={() => {
          setPopperClosed(true);
          onOutEnd();
        }}
        in={open}
        nodeRef={transitionRef}
        timeout={250}
      >
        <TransitionSlideFade ref={transitionRef}>
          <Inner>
            <Content>{children}</Content>
            {buttons.length > 0 ? <Buttons>{buttons}</Buttons> : undefined}
          </Inner>
        </TransitionSlideFade>
      </CSSTransition>
    </Popper>
  ) : (
    <Systray
      style={{
        zIndex: 1112,
        ...(!open ? { pointerEvents: 'none' } : undefined),
      }}
    >
      <CSSTransition
        onExited={() => {
          setPopperClosed(true);
          onOutEnd();
        }}
        in={open}
        nodeRef={transitionRef}
        timeout={250}
      >
        <TransitionSlideFade ref={transitionRef}>
          <Inner>
            <Content>{children}</Content>
            {buttons.length > 0 ? <Buttons>{buttons}</Buttons> : undefined}
          </Inner>
        </TransitionSlideFade>
      </CSSTransition>
    </Systray>
  );
};
