import { AxiosInstance } from 'axios';
import React from 'react';

import {
  FindToursRequest,
  FindToursResponse,
} from '../../../api/src/domain/FindTours';
import { Tour } from '../../../api/src/domain/Tour';

export class TourService {
  constructor(private readonly axios: AxiosInstance) {}

  public async getTour(): Promise<Tour | undefined> {
    const req: FindToursRequest = {
      context: {
        url: window.location.href,
      },
    };
    const response = await this.axios.post<FindToursResponse>(
      '/api/v1/tours',
      req,
    );
    return response.data.tours[0];
  }
}

export const TourServiceContext = React.createContext<TourService>(
  null as unknown as TourService,
);
