import {
  createGenerateClassName,
  styled,
  StylesProvider,
} from '@material-ui/styles';
import axiosStatic from 'axios';
import React from 'react';

import { TourStep } from '../../api/src/domain/TourStep';
import { ThemeContainer } from './components/ThemeContainer';
import { TourSequence } from './components/TourSequence';
import { TourService } from './services/TourService';

const axios = axiosStatic.create({
  baseURL: 'https://performance.ally.ac',
});
const tourService = new TourService(axios);

const Container = styled('div')({
  padding: '2em',
});

const classNameGenerator = createGenerateClassName({
  productionPrefix: 'nctv',
});

const App: React.FC = () => {
  const [tour, setTour] = React.useState<TourStep[]>();
  React.useEffect(() => {
    void tourService.getTour().then((t) => setTour(t?.steps));
  }, []);
  return (
    <StylesProvider generateClassName={classNameGenerator}>
      <ThemeContainer>
        <Container>
          <div>
            <button id="button1">Show me a tutorial 1st pls</button>
          </div>
          <div>
            <button id="button2">Show me a tutorial 2nd pls</button>
          </div>
          {tour !== undefined ? (
            <TourSequence onComplete={() => setTour(undefined)} tour={tour} />
          ) : (
            <></>
          )}
        </Container>
      </ThemeContainer>
    </StylesProvider>
  );
};

export default App;
