import React from 'react';

import { Popup } from './Popup';

export type TourStepPopupProps = {
  active: boolean;
  buttonDismissText?: string;
  buttonNextText?: string;
  html: string;
  onDismiss?: () => void;
  onNext?: () => void;
  onOut?: () => void;
  targetEl?: HTMLElement;
};

export const TourStepPopup: React.FC<TourStepPopupProps> = ({
  active,
  buttonDismissText = 'Dismiss',
  buttonNextText = 'Next',
  html,
  onDismiss = () => undefined,
  onNext,
  onOut = () => undefined,
  targetEl,
}) => {
  return (
    <Popup
      anchorEl={targetEl}
      buttons={[
        <button key="dismiss" onClick={() => onDismiss()}>
          {buttonDismissText}
        </button>,
        onNext !== undefined ? (
          <button className="nctv-primary" key="next" onClick={onNext}>
            {buttonNextText}
          </button>
        ) : undefined,
      ].filter((b) => b !== undefined)}
      open={active}
      onOutEnd={onOut}
    >
      <div dangerouslySetInnerHTML={{ __html: html }} />
    </Popup>
  );
};
