import { Placement } from '@popperjs/core';
import React from 'react';
import { usePopper } from 'react-popper';

export type PopperProps = React.HTMLAttributes<HTMLDivElement> & {
  anchorEl?: HTMLElement;
  offset?: number;
  open: boolean;
  placement?: Placement;
};

export const Popper: React.FC<PopperProps> = ({
  anchorEl,
  children,
  offset = 0,
  open,
  placement,
  ...divProps
}) => {
  const [popperEl, setPopperEl] = React.useState<HTMLElement | null>(null);
  const { attributes, styles } = usePopper(anchorEl, popperEl, {
    modifiers: [{ name: 'offset', options: { offset: [0, offset] } }],
    placement,
  });

  return (
    <div
      ref={setPopperEl}
      {...divProps}
      style={{
        ...divProps.style,
        ...styles.popper,
        ...(!open
          ? { pointerEvents: 'none', visibility: 'hidden' }
          : undefined),
      }}
      {...attributes.popper}
    >
      {children}
    </div>
  );
};
